import React, { Component } from 'react';

class Systemlogtable extends Component {

    render() {
        let RTtable = []
        let loginlog = []
        let apilog = []
        if (this.props.optionValue == 0) {
            loginlog = this.props.loginlogList
            const powerName = ['唯讀使用者', '一般使用者', '一般使用者管理員', '系統設定管理員', '超級管理者']

            RTtable = loginlog.map(
                (info, index) => {
                    let managerName = ''
                    let dateTime = info.time.split("T")[0] + ' ' + info.time.split("T")[1].split(".")[0]
                    if (info.role == 0) {
                        managerName = powerName[info.role]
                    } else if (info.role == 1) {
                        managerName = powerName[info.role]
                    } else if (info.role == 2) {
                        managerName = powerName[info.role]
                    } else if (info.role == 3) {
                        managerName = powerName[info.role]
                    } else if (info.role == 4) {
                        managerName = powerName[info.role]
                    }

                    return (
                        <div key={index} style={{ height: "50px", borderWidth: "1px", borderStyle: "solid", borderColor: "rgba(232,232,232,1)", borderTopWidth: "0px", display: "grid", gridTemplateColumns: "1fr 6fr", gridGap: '10px', paddingLeft: "10px", paddingRight: "10px" }}>
                            <div style={{ fontSize: "14px", display: "flex", alignItems: "center" }}>{dateTime}</div>
                            <div style={{ fontSize: "14px", display: "flex", alignItems: "center" }}>User {info.name}({managerName}) login system</div>
                        </div>)
                }
            )
        } else if (this.props.optionValue == 1) {
            apilog = this.props.apilogList

            RTtable = apilog.map((data, index) => {
                if (data !== null) {
                    let date = data.split("- -")[1].split("]")[0].split("[")[1].split(" ")[0].split("/")
                    const months = ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec']
                    for (let j = 0; j < months.length; j++) {
                        if (date[1] == months[j]) {
                            date[1] = months.indexOf(months[j]) + 1
                        }
                    }
                    if (date[1] < 10) {
                        date[1] = '0' + date[1]
                    }
                    let time = date[2].split(":")[0] + "-" + date[1] + "-" + date[0] + " " + date[2].split(":")[1] + ":" + date[2].split(":")[2] + ":" + date[2].split(":")[3]
                    let content = data.split("- -")[1].split("]")[1]
                    
                    return (
                        <div key={index} style={{ height: "50px", borderWidth: "1px", borderStyle: "solid", borderColor: "rgba(232,232,232,1)", borderTopWidth: "0px", display: "grid", gridTemplateColumns: "1fr 6fr", gridGap: '10px', paddingLeft: "10px", paddingRight: "10px" }}>
                            <div style={{ fontSize: "14px", display: "flex", alignItems: "center" }}>{time}</div>
                            <div style={{ fontSize: "14px", display: "flex", alignItems: "center" }}>{content}</div>
                        </div>)
                }
            })
        }

        return (
            <div>
                <div style={{ height: "50px", borderWidth: "1px", borderStyle: "solid", borderColor: "rgba(232,232,232,1)", backgroundColor: "rgba(255,249,237,1)", display: "grid", gridTemplateColumns: "1fr 6fr", gridGap: '10px', paddingLeft: "10px", paddingRight: "10px" }}>
                    <div style={{ fontSize: "14px", display: "flex", alignItems: "center" }}>紀錄時間</div>
                    <div style={{ fontSize: "14px", display: "flex", alignItems: "center" }}>事件</div>
                </div>
                <div style={{ maxHeight: "550px", overflow: "auto" }}>
                    {
                        RTtable
                    }
                </div>
            </div>
        );
    }
}

export default Systemlogtable