import React, { Component } from 'react'
import chooseedit from '../../Image/svg/Chooseedit.svg'
import './Nurtion_TPN_cell.scss'
import { yyyymmddformat } from '../../Function/Timeformat'
import RemarkModal from '../CrossSectionView/RemarkModal'

class Nurtiontpncell extends Component {
    state = {
        EditMode: false,
        onEdit: false,
        remark: this.props.data.TPNRemarks
    }
    onMouseEnter() {
        this.setState(
            {
                EditMode: true
            }
        )
    }
    onMouseLeave() {
        this.setState(
            {
                EditMode: false
            }
        )
    }
    showModal = () => {
        this.setState({
            onEdit: true,
        });
    };

    handleOk = newRemarkValue => {
        console.log(newRemarkValue);
        this.setState({
            onEdit: false,
            remark: newRemarkValue
        });
    };

    handleCancel = e => {
        console.log(e);
        this.setState({
            onEdit: false,
        });
    };
    Moduledisplay(time, title) {
        return (
            <RemarkModal caseinfo={this.props.caseinfo} type="nurtiontpn" createTime={time} name={title} visible={this.state.onEdit} remark={this.state.remark} onCancel={this.handleCancel} onOk={this.handleOk}></RemarkModal>
        );
    }




    render() {
        const { data } = this.props
        const TPNList = ["TPN", "AA", "Glu", "Na", "K", "Ca", "P", "Mg"]
        const TPNListcellRender = TPNList.map(function (info, index) {
            return (
                <centerstyle key={index} style={{ border: "1px solid rgba(232,232,232,1)", borderCollapse: "collapse" }}>
                    <t2>{info}</t2>
                </centerstyle>
            );
        })
        const TPNSubData = ["8", "7", "6", "5", "4", "3", "2", "1"]
        const TPNSubDatacellRender = TPNSubData.map(function (info, index) {
            return (
                <centerstyle key={index} style={{ border: "1px solid rgba(232,232,232,1)", borderCollapse: "collapse" }}>
                    <t2>{info}</t2>
                </centerstyle>
            );
        })
        const RemarkData = this.state.remark
        function RemarkNull(value) {
            console.log(value)
            if (value === null || value === undefined || value === "") {
                return "none"
            }
            return "flex"
        }
        function RemarkNullheight(value) {
            console.log(value)
            if (value === null || value === undefined || value === "") {
                return "90px"
            }
            return "120px"
        }
        return (
            <div onMouseEnter={() => this.onMouseEnter()} onMouseLeave={() => this.onMouseLeave()} className='nurtiontpntablelayout'>
                <div className='nurtionTPNogdercell' >
                    <div className='cellfordisposename'>
                        <div className='tpncelltextdisplay'>
                            <t3>{data.TPNDispositionName}</t3>
                        </div>
                        <div style={{ display: "grid", gridTemplateRows: "1fr 1fr" }}>
                            <div style={{ display: "grid", gridTemplateColumns: "repeat(8,1fr)" }}>
                                {TPNListcellRender}
                            </div>
                            <div style={{ display: "grid", gridTemplateColumns: "repeat(8,1fr)" }}>
                                {TPNSubDatacellRender}
                            </div>
                        </div>
                    </div>
                    <div style={this.state.EditMode ? { display: "flex", alignItems: "center", justifyContent: "center", position: "absolute", right: 0, height: RemarkNullheight(RemarkData), background: "white", width: "77px", border: "rgba(238,238,238,1) 1px solid" } : { display: "none" }}>

                        <div style={{ width: "40px", height: "40px", borderRadius: "99em", background: "rgba(124, 179, 5, 1)", display: "flex", justifyContent: "center", alignItems: "center" }}>
                            <img src={chooseedit} alt='chooseedit'></img>
                        </div>
                    </div>
                </div>

                <div style={{ display: RemarkNull(RemarkData), border: "1px solid rgba(232,232,232,1)", borderCollapse: "collapse", padding: "0px 5px", alignItems: "center", height: "50px" }}>
                    <t3>{RemarkData}</t3>
                </div>
                {this.Moduledisplay(data.TPNStartTime, data.TPNDispositionName)}
            </div>
        );
    }
}

export default Nurtiontpncell
