import React, { Component } from 'react';
import './ExamNonlabtablecell.scss'
// import { Nonlablist } from '../../Data/Wardviewdata'
import moment from 'moment'

class Examnonlabviewtable extends Component {
    render() {
        function listforstatus(ordlist, ressect, pickerdaterange) {
            var status_report = []
            console.log(ordlist, ressect, pickerdaterange)
            const et = pickerdaterange[1].valueOf() + (24 * 60 * 60 * 1000)
            const st = pickerdaterange[0].valueOf()
            if (Array.isArray(ordlist) && Array.isArray(ressect)) {
                for (let index = 0; index < ordlist.length; index++) {
                    const element = ordlist[index];
                    const time = element.ORBGNDT.toString().trim() + element.ORBGNTM.toString().trim()
                    const timeStamp = moment(time, 'YYYY-MM-DDHHmmss').valueOf()
                    if (timeStamp > st && timeStamp < et) {
                        const status = ressect.find(function (value, index, array) {
                            return value.ORDCLNM === element.ORDCLNM
                        })
                        if (status) {
                            status_report.push({
                                "status": true,
                                "element": element
                            })
                        }
                        else {
                            status_report.push({
                                "status": false,
                                "element": element
                            })

                        }
                    }
                }
            }
            return status_report
        }
        function statusflag(status) {
            switch (status) {
                case 0:
                    return '已開單'
                case 1:
                    return '已簽收'
                case 2:
                    return '已發報告'
                default:
                    return '--'
            }
        }
        const { PickerDateRange, ORDLIST, RESSECT } = this.props
        //Picker Date 前三天
        const list = listforstatus(ORDLIST, RESSECT, PickerDateRange)
        console.log(list)
        const nonlabcell = list.map(function (value, index, array) {
            console.log(value)
            const time = value.element.ORBGNDT.toString().trim() + value.element.ORBGNTM.toString().trim()
            const name = value.element.ORPROCED
            const status = value.status
            return (
                <div key={index} style={{ height: "50px", display: "grid", gridTemplateColumns: "1fr 1fr 1fr 1fr" }}>
                    <t3 className='nonlabteblecell'>{moment(time, 'YYYY-MM-DD').format('YYYY/MM/DD')}</t3>
                    <t3 className='nonlabteblecell'>{name}</t3>
                    <t3 className='nonlabteblecell'>{status ? "已發報告" : "已開單"}</t3>
                    <t3 className='nonlabteblecell'>{"Result parse"}</t3>
                </div>
            );
        })

        return (
            <div style={{ marginTop: "10px" }}>
                <div style={{ height: "50px", border: "#E2EDF3 1px solid", backgroundColor: "#E2EDF3", display: "grid", gridTemplateColumns: "1fr 1fr 1fr 1fr" }}>
                    <t3 className='nonlabtebletitlecell'>檢驗日期</t3>
                    <t3 className='nonlabtebletitlecell'>檢驗項目名稱</t3>
                    <t3 className='nonlabtebletitlecell'>檢驗項目狀態</t3>
                    <t3 className='nonlabtebletitlecell'>檢驗結果</t3>
                </div>
                <div className='nonlabviewtable'>
                    {nonlabcell}
                </div>
            </div>
        );
    }
}

export default Examnonlabviewtable